body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button:focus{
  outline: 0;
}

.main-container {
  font-family: "Roboto Mono", Consolas, "Times New Roman", sans-serif;
  background: #009999 -webkit-gradient(linear, left top, right bottom, from(#009999), to(aliceblue));
  background: #009999 -webkit-linear-gradient(top left, #009999, aliceblue);
  background: #009999 linear-gradient(to bottom right, #009999, aliceblue);
  padding: 0;
  margin: 0;
}

.container {
  color: #555;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0 auto 2em auto;
  justify-content: flex-start;
  align-items: center;
  align-self: center;
}

footer {
  background: white;
}

.btn {
  border: 0.01em solid #555555;
}

.btn:hover{
  border: 0.01em solid #eeeeee;
}

@font-face {
  font-family: Digital-7;
  src: url(/pomodoro-clock/static/media/digital-7.1e670d88.ttf);
}

@font-face {
  font-family: 'Digital-7 Mono';
  src: url("/pomodoro-clock/static/media/digital-7 (mono).58045dab.ttf");
}

.display {
  font-family: "Digital-7 Mono", Digital-7, monospace;
  font-size: 2em;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

